import React from 'react';
import Image from 'next/image';
import Link from 'next/link';
import './CertificateCard.css';

interface CertificateCardProps {
  title: string;
  issuer: string;
  imageUrl: string;
  certificateUrl: string;
  href?: string; // optional route to view page
}

const CertificateCard: React.FC<CertificateCardProps> = ({ title, issuer, imageUrl, certificateUrl, href }) => {
  const content = (
    <div className="certificate-card">
      <div className="certificate-image-container">
        <Image src={imageUrl} alt={title} fill sizes="(max-width: 640px) 100vw, (max-width: 1024px) 50vw, 33vw" className="certificate-image" />
      </div>
      <div className="certificate-info">
        <h3 className="certificate-title">{title}</h3>
        <p className="certificate-issuer">{issuer}</p>
      </div>
    </div>
  );

  // If href is provided, navigate to the internal view page; otherwise fall back to opening the certificateUrl
  if (href) {
    return (
      <Link href={href} className="certificate-card-link" aria-label={`${title} by ${issuer}`}>
        {content}
      </Link>
    );
  }

  return (
    <a href={certificateUrl} target="_blank" rel="noopener noreferrer" className="certificate-card-link" aria-label={`${title} by ${issuer}`}>
      {content}
    </a>
  );
};

export default CertificateCard;
