"use client";

import CertificateCard from '../../components/CertificateCard';
import AnimatedContent from '../../components/AnimatedContent';
import SplitText from '../../components/SplitText';
import ClickSpark from '../../components/ClickSpark';
import TargetCursor from '../../components/TargetCursor';
import { certificates } from '../../data/certificates';

export default function CertificatesPage() {
  return (
    <ClickSpark sparkColor="#ffffff" sparkSize={10} sparkRadius={15} sparkCount={10} duration={450}>
      <main className="px-6 sm:px-10 py-10 mx-auto max-w-6xl">
      <TargetCursor spinDuration={2} hideDefaultCursor={true} />
      <section>
        <AnimatedContent distance={80} direction="vertical" duration={1.2} initialOpacity={0} animateOpacity>
          <div className="mb-8 sm:mb-10 text-center">
            <SplitText
              text="My Certificates"
              className="text-3xl sm:text-4xl md:text-5xl font-extrabold tracking-tight"
              delay={160}
              duration={1.2}
              ease="power3.out"
              splitType="chars"
              from={{ opacity: 0, y: 40 }}
              to={{ opacity: 1, y: 0 }}
              threshold={0.1}
              rootMargin="-100px"
              textAlign="center"
            />
          </div>
        </AnimatedContent>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {certificates.map((cert, index) => (
            <AnimatedContent key={index} distance={60} direction="vertical" duration={0.6} initialOpacity={0} animateOpacity delay={index * 0.1}>
              <div className="cursor-target">
                <CertificateCard
                  title={cert.title}
                  issuer={cert.issuer}
                  imageUrl={cert.imageUrl}
                  certificateUrl={cert.certificateUrl}
                />
              </div>
            </AnimatedContent>
          ))}
        </div>
      </section>
    </main>
    </ClickSpark>
  );
}
